:root {
  --font-body: "Lato", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --brown: hsl(29, 85%, 35%);
  --brown-light:rgba(165, 87, 13, 0.17);
  --brown-lighter:rgba(165, 87, 13, 0.37);
  --brown-dark:hsla(30, 77%, 36%, 1);
  --brown-darker: hsla(29, 84%, 25%, 1);
  --pink-light:hsla(29, 82%, 95%, 1);
  --pink-lighter:hsla(29, 100%, 81%, 0.47);
  --brown--lighter:rgba(112, 55, 5, 0.85);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

.title {
  font-weight: 900;
  font-size: 2.25rem;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 59.375em) {
  .title {
    font-size: 1.875rem;
  }
}
@media (max-width: 39.9375em) {
  .title {
    font-size: 1.25rem;
  }
}
.title span {
  font-weight: 900;
}
.title--brown {
  color: var(--brown);
  font-weight: 300;
}
.title--white {
  color: var(--white);
  font-weight: 300;
}

.paragraph {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 177%;
}
.paragraph--white {
  color: var(--white);
}
.paragraph--black {
  color: var(--black);
}
@media (max-width: 59.375em) {
  .paragraph {
    font-size: 0.8125rem;
  }
}
@media (max-width: 39.9375em) {
  .paragraph {
    font-size: 0.75rem;
  }
}

.container {
  padding: 0 8.75rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (max-width: 59.375em) {
  .container {
    padding: 0 3.125rem;
  }
}
@media (max-width: 39.9375em) {
  .container {
    padding: 0 1.25rem;
  }
}

.mt-3 {
  margin-top: 3rem;
}

.banner {
  width: 100%;
  clip-path: polygon(0 0, 100% 0, 100% 95vh, 0 100%);
  height: 105vh;
  color: var(--white);
}
@media (max-width: 39.9375em) {
  .banner {
    height: 100vh;
  }
}
.banner__container {
  position: relative;
  overflow: hidden;
}
.banner__slider {
  position: relative;
  height: 105vh;
  width: 100%;
}
@media (max-width: 39.9375em) {
  .banner__slider {
    height: 100vh;
  }
}
.banner__slider--one {
  background: linear-gradient(rgba(111, 55, 5, 0.38), rgba(111, 55, 5, 0.38)), url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1667382172/dkfarm/accueil/banner1.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.banner__slider--two {
  background: linear-gradient(rgba(111, 55, 5, 0.38), rgba(111, 55, 5, 0.38)), url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1667382184/dkfarm/accueil/banner2.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.banner__slider--three {
  background: linear-gradient(rgba(111, 55, 5, 0.38), rgba(111, 55, 5, 0.38)), url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1667382186/dkfarm/accueil/banner3.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.banner__slider--four {
  background: linear-gradient(rgba(111, 55, 5, 0.38), rgba(111, 55, 5, 0.38)), url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1667396672/dkfarm/accueil/banner4.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.banner__container > div {
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 0 70px 0;
  opacity: 0;
  transition: opacity 3s;
}
.banner__container > div.inactive {
  opacity: 0;
}
.banner__container > div.inactive .banner__title {
  animation: text-out-left 2.5s ease-in-out forwards;
}
.banner__container > div.inactive .banner__button {
  animation: text-out-right 2.5s ease-in-out forwards;
}
.banner__container > div.inactive .banner__text {
  opacity: 0;
  transition: opacity 3s;
  visibility: hidden;
  pointer-events: none;
}
.banner__container > div.active {
  position: relative;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}
.banner__container > div.active .banner__title {
  animation: text-in-left 2.5s ease-in-out forwards;
}
.banner__container > div.active .banner__button {
  animation: text-in-right 2.5s ease-in-out forwards;
}
.banner__description {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  justify-items: center;
}
@media (max-width: 59.375em) {
  .banner__description {
    width: 100%;
  }
}
@media (max-width: 39.9375em) {
  .banner__description {
    left: 56%;
    justify-items: flex-start;
    text-align: left;
  }
}
.banner__description p {
  width: 65%;
}
.banner__title {
  font-weight: 800;
  font-size: 3rem;
  text-transform: uppercase;
}
@media (max-width: 39.9375em) {
  .banner__title {
    word-break: break-word;
    line-height: 3.625rem;
    padding-right: 1rem;
  }
}
.banner__text {
  font-weight: 400;
  font-size: 0.875rem;
  padding: 0 3rem;
  text-align: center;
}
@media (max-width: 39.9375em) {
  .banner__text {
    text-align: left;
    padding: 0;
  }
}
@keyframes text-in-left {
  from {
    opacity: 0;
    transform: translateX(-500%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes text-in-right {
  from {
    opacity: 0;
    transform: translateX(500%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes text-out-left {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-500%);
  }
}
@keyframes text-out-right {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(500%);
  }
}

.navigation__checkbox {
  display: none;
}
.navigation__logo {
  top: 2.7rem;
  z-index: 50;
  position: fixed;
  left: 4rem;
}
@media (max-width: 39.9375em) {
  .navigation__logo {
    left: 1rem;
    top: 1rem;
    width: 5rem;
  }
}
.navigation__button {
  background-color: var(--white);
  filter: drop-shadow(0px 2px 11px rgba(0, 0, 0, 0.25));
  height: 3.4rem;
  width: 3.4rem;
  position: fixed;
  top: 3.5rem;
  right: 4rem;
  border-radius: 50%;
  z-index: 2000;
  text-align: center;
  cursor: pointer;
}
@media (max-width: 39.9375em) {
  .navigation__button {
    right: 1rem;
    top: 1.5rem;
  }
}
.navigation__background {
  height: 3.4rem;
  width: 3.4rem;
  border-radius: 50%;
  position: fixed;
  top: 3.5rem;
  right: 4rem;
  background-image: radial-gradient(var(--brown), var(--brown-dark));
  z-index: 1000;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
@media (max-width: 39.9375em) {
  .navigation__background {
    right: 1rem;
    top: 1.5rem;
  }
}
.navigation__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 0;
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.navigation__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
  width: 100%;
}
.navigation__item {
  margin-bottom: 0.3rem;
}
.navigation__link:link, .navigation__link:visited {
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 400;
  padding: 1rem 2rem;
  color: var(--white);
  text-decoration: none;
  text-transform: uppercase;
  background-image: linear-gradient(120deg, transparent 0%, transparent 50%, var(--white) 50%);
  background-size: 250%;
  transition: all 0.4s;
}
.navigation__link:hover, .navigation__link:active {
  background-position: 100%;
  color: var(--brown);
  transform: translateX(1rem);
}
.navigation__checkbox:checked ~ .navigation__background {
  transform: scale(80);
}
.navigation__checkbox:checked ~ .navigation__nav {
  visibility: visible;
  pointer-events: initial;
  opacity: 1;
  width: 100%;
}
.navigation__icon {
  position: relative;
  margin-top: 1.6rem;
}
.navigation__icon, .navigation__icon::before, .navigation__icon::after {
  width: 1.4rem;
  height: 0.1875rem;
  background-color: var(--black);
  display: inline-block;
}
.navigation__icon::before, .navigation__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all 0.2s;
}
.navigation__icon::before {
  top: -0.5rem;
}
.navigation__icon::after {
  top: 0.5rem;
}
.navigation__button:hover .navigation__icon::before {
  top: -0.6rem;
}
.navigation__button:hover .navigation__icon::after {
  top: 0.6rem;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}

